const HeaderStyle = {
    display : 'flex',
    justifyContent: 'left',
    alignItems: 'center',
    backgroundColor : '#888',
    height : '100px'
}

const LogoStyle = {
  fontSize : '30px',
  marginLeft : '15px'
}

function Header(){
  return(
    <header className="header" style={HeaderStyle}>
      <div className="logo" style={LogoStyle}>Todo Application</div>
      <div>나의 성취 보기</div>
    </header>
  )
}

export default Header
